___CSS_LOADER_EXPORT___.push([module.id, `body.checkout-root {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  background: linear-gradient(135deg, #0f172a 0%, #1e293b 40%, #111827 100%);
  color: #e2e8f0;
  min-height: 100vh;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px;
}

.checkout-container {
  background: #0b1220;
  width: min(520px, 94vw);
  border-radius: 16px;
  border: 1px solid rgba(148, 163, 184, 0.14);
  box-shadow: 0 20px 60px rgba(0,0,0,0.35);
  padding: 20px 22px;
}

.checkout-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
}

.checkout-header h2 {
  margin: 0;
  font-size: 20px;
  font-weight: 700;
  color: #f8fafc;
}

.checkout-header button {
  background: rgba(248, 250, 252, 0.08);
  border: 1px solid rgba(148, 163, 184, 0.25);
  color: #f8fafc;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  cursor: pointer;
  font-size: 18px;
}

.checkout-form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.checkout-form label {
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 13px;
  color: #cbd5e1;
}

.checkout-form input,
.checkout-form select {
  padding: 10px 12px;
  border-radius: 10px;
  border: 1px solid rgba(148, 163, 184, 0.35);
  background: rgba(15, 23, 42, 0.6);
  color: #e2e8f0;
  font-size: 14px;
}

.method-toggle {
  display: flex;
  gap: 14px;
  margin: 6px 0 4px;
}

.method-toggle label {
  flex-direction: row;
  gap: 8px;
  align-items: center;
}

.card-fields {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.card-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}

.checkout-form button[type="submit"] {
  margin-top: 8px;
  padding: 12px;
  border: none;
  border-radius: 12px;
  background: linear-gradient(135deg, #22c55e, #16a34a);
  color: #0b1220;
  font-size: 15px;
  font-weight: 700;
  cursor: pointer;
  transition: transform 120ms ease, box-shadow 120ms ease;
}

.checkout-form button[type="submit"]:disabled {
  background: rgba(148, 163, 184, 0.45);
  color: #0f172a;
  cursor: not-allowed;
}

.checkout-form button[type="submit"]:not(:disabled):hover {
  transform: translateY(-1px);
  box-shadow: 0 8px 24px rgba(34, 197, 94, 0.35);
}

.error {
  color: #f97316;
  font-size: 13px;
  background: rgba(249, 115, 22, 0.12);
  border: 1px solid rgba(249, 115, 22, 0.4);
  border-radius: 10px;
  padding: 8px 10px;
}

@media (max-width: 520px) {
  .checkout-container {
    padding: 18px 16px;
  }

  .card-row {
    grid-template-columns: 1fr;
  }
}
