import React, { useEffect, useState } from 'react';

const initialForm = {
  amount: '',
  currency: 'USD',
  method: 'CARD',
  cardNumber: '',
  cardCvv: '',
  cardExpiry: '',
  upiId: '',
};

export default function CheckoutForm() {
  const [form, setForm] = useState(initialForm);
  const [submitting, setSubmitting] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    // Reset errors when method changes
    setError('');
  }, [form.method]);

  const postMessage = (type, payload) => {
    window.parent.postMessage({ type, payload }, window.location.origin);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value }));
  };

  const validate = () => {
    if (!form.amount || Number.isNaN(Number(form.amount))) {
      setError('Amount is required');
      return false;
    }
    if (form.method === 'CARD') {
      if (!form.cardNumber || form.cardNumber.length < 12) {
        setError('Card number invalid');
        return false;
      }
      if (!form.cardExpiry) {
        setError('Expiry required');
        return false;
      }
      if (!form.cardCvv || form.cardCvv.length < 3) {
        setError('CVV invalid');
        return false;
      }
    }
    if (form.method === 'UPI') {
      if (!form.upiId || !form.upiId.includes('@')) {
        setError('UPI ID invalid');
        return false;
      }
    }
    return true;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!validate()) return;

    setSubmitting(true);
    setError('');
    try {
      // Fake processing for demo; in real scenario call backend
      await new Promise((resolve) => setTimeout(resolve, 800));
      postMessage('payment_success', {
        amount: Number(form.amount),
        currency: form.currency,
        method: form.method,
        cardLast4: form.cardNumber.slice(-4),
        upiId: form.upiId,
        status: 'captured',
      });
    } catch (err) {
      postMessage('payment_failed', { message: err.message || 'Payment failed' });
    } finally {
      setSubmitting(false);
    }
  };

  const handleClose = () => {
    postMessage('payment_closed');
  };

  return (
    <div className="checkout-container">
      <div className="checkout-header">
        <h2>Complete Payment</h2>
        <button type="button" onClick={handleClose} aria-label="Close">x</button>
      </div>
      <form onSubmit={handleSubmit} className="checkout-form">
        <label>
          Amount
          <input name="amount" type="number" value={form.amount} onChange={handleChange} required />
        </label>
        <label>
          Currency
          <select name="currency" value={form.currency} onChange={handleChange}>
            <option value="USD">USD</option>
            <option value="EUR">EUR</option>
            <option value="INR">INR</option>
          </select>
        </label>

        <div className="method-toggle">
          <label>
            <input
              type="radio"
              name="method"
              value="CARD"
              checked={form.method === 'CARD'}
              onChange={handleChange}
            />
            Card
          </label>
          <label>
            <input
              type="radio"
              name="method"
              value="UPI"
              checked={form.method === 'UPI'}
              onChange={handleChange}
            />
            UPI
          </label>
        </div>

        {form.method === 'CARD' && (
          <div className="card-fields">
            <label>
              Card Number
              <input name="cardNumber" value={form.cardNumber} onChange={handleChange} required />
            </label>
            <div className="card-row">
              <label>
                Expiry (MM/YY)
                <input name="cardExpiry" value={form.cardExpiry} onChange={handleChange} placeholder="12/29" required />
              </label>
              <label>
                CVV
                <input name="cardCvv" value={form.cardCvv} onChange={handleChange} placeholder="123" required />
              </label>
            </div>
          </div>
        )}

        {form.method === 'UPI' && (
          <label>
            UPI ID
            <input name="upiId" value={form.upiId} onChange={handleChange} placeholder="name@bank" required />
          </label>
        )}

        {error && <div className="error">{error}</div>}

        <button type="submit" disabled={submitting}>
          {submitting ? 'Processing...' : 'Pay Now'}
        </button>
      </form>
    </div>
  );
}
